import { createRouter, createWebHashHistory } from "vue-router";
import Login from './components/Login.vue';
import Logout from './components/Logout.vue';
import Register from './components/Register.vue';
import AddShipment from './components/AddShipment.vue';
import myShipments from './components/myShipments.vue';
import ShipmentDetails from './components/ShipmentDetails.vue';
import CheckOut from "./components/CheckOut.vue";
import CheckOutReturn from "./components/CheckOutReturn.vue";

const routes = [
    {
        name: 'Login',
        path: '/Login',
        component: Login,
        meta: {public: true}
    },
    {
        name: 'Logout',
        path: '/logout',
        component: Logout,
        meta: {public: true}
    },
    {
        name: 'Register',
        path: '/Register',
        component: Register,
        meta: {public: true}
    },
    {
        name: 'AddShipment',
        path: '/add-shipment',
        component: AddShipment,
        meta: {public: true}
    },
    {
        name: 'myShipments',
        path: '/my-shipments',
        component: myShipments,
        meta: {public: false}
    },
    {
        name: 'ShipmentDetails',
        path: '/shipment-details/:orderNumber',
        component: ShipmentDetails,
        meta: {public: false},
        props: true
    },
    {
        name: 'Home',
        path: '/',
        component: AddShipment,
        meta: {public: true}
    },
    {
        name: 'Checkout',
        path: '/checkout/:orderNumberPrice',
        component: CheckOut,
        meta: {public: true},
        props: true
    },
    {
        name: 'CheckoutReturn',
        path: '/checkout-return/:session_id/:orderNumber/:userId',
        component: CheckOutReturn,
        meta: {public: true},
        props: true
    }
];

const router = createRouter({
    history: createWebHashHistory(),
    routes
});

