<template>
    <h1>Add Shipment</h1>
    <div v-if="!courierSelected">
        <div class="form-container">
            <form @submit.prevent="getMinPrice" class="add-shipment-form">
                <div class="ship-codes-container">
                    <div class="selected-codes">
                        <span id="slectedSenderCode">{{ selectedCodeSender }}</span>
                        <span id="slectedReceiverCode">{{ selectedCodeReceiver }}</span>
                    </div>
                    <div class="group-codes">
                        <div>
                            <input class="input-field" type="text" @click="filterByCode('sender')" v-on:keyup="filterByCode('sender')" v-model="senderCode" placeholder="Sender Zip Code" required />
                            <p v-if="errors.senderCode" class="errors">{{ errors.senderCode }}</p>
                            <Transition name="slide-fade">
                                <div class="codes-select-sender">
                                    <!-- <span>Test 1</span> -->
                                    <div class="codes" v-if="codesFoundSender.length > 0 && codesFoundSender[0].id > 0">
                                        <div class="code" v-for="code in codesFoundSender" :key="code.id" @click="zipCodeUpdate('sender', code.CODE, code.CITY, code.PROVINCE)">
                                            <span class="code-text">{{ code.CODE }} {{ code.CITY }} ({{ code.PROVINCE }})</span>
                                        </div>
                                    </div>
                                    <div class="codes empty" v-if="codesFoundSender.length > 0 && codesFoundSender[0].id === undefined">
                                        <div class="code">
                                            <span class="code-text">{{ codesFoundSender[0] }}</span>
                                        </div>
                                    </div>
                                </div>
                            </Transition>
                        </div>
                        <div class="button-reverse" @click="reverseZipCodes">(X)</div>
                        <div>
                            <input class="input-field receiver-code" type="text" @click="filterByCode('receiver')" v-on:keyup="filterByCode('receiver')" v-model="receiverCode" placeholder="Receiver Zip Code" required />
                            <p v-if="errors.receiverCode" class="errors">{{ errors.receiverCode }}</p>
                            <Transition name="slide-fade">
                                <div class="codes-select-receiver">
                                    <!-- <span>Test 2</span> -->
                                    <div class="codes" v-if="codesFoundReceiver.length > 0 && codesFoundReceiver[0].id > 0">
                                        <div class="code" v-for="code in codesFoundReceiver" :key="code.id" @click="zipCodeUpdate('receiver', code.CODE, code.CITY, code.PROVINCE)">
                                            <span class="code-text">{{ code.CODE }} {{ code.CITY }} ({{ code.PROVINCE }})</span>
                                        </div>
                                    </div>
                                    <div class="codes empty" v-if="codesFoundReceiver.length > 0 && codesFoundReceiver[0].id === undefined">
                                        <div class="code">
                                            <span class="code-text">{{ codesFoundReceiver[0] }}</span>
                                        </div>
                                    </div>
                                </div>
                            </Transition>
                        </div>
                    </div>
                </div>
                <input class="input-field" type="number" v-model="weight" placeholder="Weight in kg" required />
                <p v-if="errors.weight" class="errors">{{ errors.weight }}</p>
                <input class="input-field" type="number" v-model="length" placeholder="Length in cm" required />
                <p v-if="errors.length" class="errors">{{ errors.length }}</p>
                <input class="input-field" type="number" v-model="width" placeholder="Width in cm" required />
                <p v-if="errors.width" class="errors">{{ errors.width }}</p>
                <input class="input-field" type="number" v-model="height" placeholder="Height in cm" required />
                <p v-if="errors.height" class="errors">{{ errors.height }}</p>
                <button type="submit">Ship{{ minPriceText }}</button>
            </form>
        </div>
    </div>
    <div v-if="courierSelected">
        <h2>List Couriers</h2>
        <div class="container">
            <div class="row">
                <div class="col-md-4">
                    <ul class="well" v-for="item in listCouries" v-bind:key="item.id">
                        <li>
                            <form @submit.prevent="addShipment(item.PRICE, item.COURIER)" class="add-shipment-form">
                                <span>{{ item.COURIER }}</span>
                                <span>$ {{ item.PRICE }}</span>
                                <input type="hidden" v-model="item.PRICE" name="price">
                                <input type="hidden" v-model="item.COURIER" name="courier">
                                <button type="submit">Select</button>
                            </form>
                        </li>
                    </ul>    
                </div>
            </div>
        </div>
    </div>
</template>
<style>
form {
    width: 400px;
}
input.receiver-code {
    text-align: right;
}
div.selected-codes {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
}
div.selected-codes > span {
    /* max-width: 50%;
    flex: 0 0 50%; */
    font-size: 14px;
}
div.group-codes {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
}
div.codes-select-sender, div.codes-select-receiver {
    position: relative;
    width: inherit;
    display: contents;
}
.button-reverse {
    width: 22px;
    cursor: pointer;
}
div.codes {
    position: absolute;
    background-color: lightskyblue;
    z-index: 9;
    width: inherit;
    text-align: left;
    padding: 2px 10px;
    text-indent: 5px;
    font-weight: 600;
    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-items: flex-start;
    max-height: 300px;
    overflow: scroll;
    border-radius: 3px;
}
div.codes > div.code {
    width: inherit;
    cursor: pointer;
    z-index: 10;
    border-bottom: 0.3px solid #111111;
    padding: 3px 0;
    margin: 2px 0;
}
div.codes > div.code:last-child {
    border-bottom: none;
    padding-bottom: 0;
    margin-bottom: 0;
}
@media screen and (max-width: 420px) {
        form.add-shipment-form {
            width: 100%;
        }
        div.ship-codes-container {
            display: flex;
            flex-direction: row-reverse;
            flex-wrap: nowrap;
            justify-content: space-between;
            align-items: stretch;
            max-width: 100%;
            flex: 0 0 100%;
        }
        div.selected-codes {
            display: flex;
            flex-direction: column;
            flex-wrap: nowrap;
            justify-content: space-around;
            align-items: stretch;
            margin-left: 10px;
            /* flex: 0 0 40%;
            max-width: 40%; */
        }
        div.group-codes {
            flex-wrap: wrap;
            justify-content: center;
            flex-direction: column;
            flex: 1;
            /* flex: 0 0 60%;
            max-width: 60%; */
        }
        div.group-codes > div {
            width: 100%;
            flex: 1;
        }
        div.group-codes > div > input.input-field {
            width: calc(100% - 20px);
        }
    }
/*
  Enter and leave animations can use different
  durations and timing functions.
*/
.slide-fade-enter-active {
  transition: all 0.3s ease-out;
}

.slide-fade-leave-active {
  transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
}

.slide-fade-enter-from,
.slide-fade-leave-to {
  transform: translateX(20px);
  opacity: 0;
}
</style>
<script>
import axios from '@/axios';

export default {
    data() {
        return {
            orderNumber: "",
            senderCode: "",
            senderCity: "",
            senderProvince: "",
            senderCountry: "",
            receiverCode: "",
            receiverCity: "",
            receiverProvince: "",
            receiverCountry: "",
            weight: "",
            length: "",
            width: "",
            height: "",
            volume: 0,
            minPrice: 0,
            minPriceText: "",
            courierSelected: false,
            listCouries: [],
            courier: "",
            price: 0,
            userID: null,
            shipment: {},
            codesFound: [],
            codesFoundSender: [],
            codesFoundReceiver: [],
            selectedCodeSender: "",
            selectedCodeReceiver: "",
            errors: {}
        }
    },
    methods: {
        validateInput() {
            const errors = {};
            if(!this.senderCode) errors.senderCode = 'The Sender Zip Code is required!';
            if(!this.receiverCode) errors.receiverCode = 'The Receiver Zip Code is required!';
            if(!this.weight) errors.weight = 'The Weight is required!';
            if(!this.length) errors.length = 'The Length is required!';
            if(!this.width) errors.width = 'The Width is required!';
            if(!this.height) errors.height = 'The Height is required!';
            return errors;
        },
        reverseZipCodes() {
            const valSelectedSenderCode = this.selectedCodeSender;
            const valSelectedRceiverCode = this.selectedCodeReceiver;
            this.selectedCodeSender = valSelectedRceiverCode;
            this.selectedCodeReceiver = valSelectedSenderCode;
            const valSenderCode = this.senderCode;
            const valRceiverCode = this.receiverCode;
            this.senderCode = valRceiverCode;
            this.receiverCode = valSenderCode;
        },
        async zipCodeUpdate(codeType, code, city, province) {
            console.log(codeType, code, city, province);
            if(codeType == 'sender') {
                this.senderCode = code;
                this.codesFoundSender = [];
                this.selectedCodeSender = city+' ('+province+')';
                this.senderCity = city;
                this.senderProvince = province;
                this.senderCountry  = 'ITALIA';
            }
            else {
                this.receiverCode = code;
                this.codesFoundReceiver = [];
                this.selectedCodeReceiver = city+' ('+province+')';
                this.receiverCity = city;
                this.receiverProvince = province;
                this.receiverCountry = 'ITALIA';
            }
        },
        async filterByCode(codeType) {
            console.log("this.codesFound 1", this.codesFound);
            this.codesFound = [];
            this.codesFoundSender = [];
            this.codesFoundReceiver = [];
            const codeLength = codeType == 'sender' ? this.senderCode.length : this.receiverCode.length;
            if(codeLength > 2) {
                const payload = { code: codeType == 'sender' ? this.senderCode : this.receiverCode };
                this.codesFound = await axios.post("/cites-by-code", payload);
                if(this.codesFound.data && this.codesFound.data.length > 0) {
                    console.log("codesFound.data", this.codesFound.data);
                    this.codesFound = this.codesFound.data;
                    // return codesFound.filter(code => {
                    //     return code.CITY.includes(this.senderCode);
                    // });
                }
                else {
                    this.codesFound = ["Nothing found!"];
                    console.log("codesFound.data", "Nothing found!");
                    // return "No city found!";
                }
            }
            else if(codeLength == 0) {
                this.codesFound = [];
            }
            else {
                this.codesFound = ["Loading..."];
                console.log("codesFound.data", "Loading...");
                // return "Loading...";
            }
            console.log("this.codesFound 2", this.codesFound);
            if(codeType == 'sender') {
                this.codesFoundSender = this.codesFound;
            }
            else if(codeType == 'receiver') {
                this.codesFoundReceiver = this.codesFound;
            }
            console.log("this.codesFound 3", this.codesFound);
        },
        async getMinPrice() {
            if(this.minPrice == 0) {
                const response = await axios.post("/get-prices", {
                    WEIGHT: this.weight, 
                    VOLUME: (this.length * this.width * this.height) / 5000
                });
                console.log(response);
                if(response.data.length > 0) {
                    console.log(response.data);
                    this.listCouries = response.data;
                    this.minPriceText = ' From $ '+response.data[0].PRICE;
                    this.minPrice = response.data[0].PRICE;
                    this.volume = (this.length * this.width * this.height) / 5000;
                }
                else {
                    console.log(response.data);
                    this.listCouries = [];
                    this.minPriceText = 'ment Price Not Available!';
                    this.minPrice = 0;
                    this.volume = 0;
                }
            }
            else {
                if(this.minPrice > 0 && this.courierSelected) {
                    // this.addShipment();
                    console.log("go to addShipment");
                }
                else if(this.minPrice > 0) {
                    this.courierSelected = true;
                    // this.listCouriers();
                    console.log("go to listCouriers");
                }
                else {
                    this.courierSelected = false;
                    console.log("Price not found!");
                }
            }
        },
        async addShipment(selectedPrice, selectedCourier) {
            this.price = selectedPrice;
            this.courier = selectedCourier;
            const errors = this.validateInput();
            const currentDate = new Date().setDate(new Date().getMinutes());
            if(Object.keys(errors).length > 0) {
                this.errors = errors;
                return;
            }
            try {
                if(localStorage.getItem('token')) {
                    if(currentDate > localStorage.getItem('tokenExpireAt')) {
                        localStorage.removeItem('token');
                        localStorage.removeItem('tokenExpireAt');
                        this.$router.push('/login');
                    }
                    else {
                        const user = await axios.get("https://arsil-backend-anzxy.sevalla.app/api/user", {
                            headers: {
                                'Authorization': `Bearer ${localStorage.getItem('token')}`
                            }
                        });
                        this.userID = user.data.id;
                        console.log("userID", this.userID);
                    }
                }
                const response = await axios.post("/insert-shipment", {
                    ORDER_NUMBER : Math.floor(Math.random() * (9999999999 - 1000000000 + 1) ) + 1000000000,
                    SENDER_CODE: this.senderCode,
                    SENDER_CITY: this.senderCity,
                    SENDER_PROVINCE: this.senderProvince,
                    SENDER_COUNTRY: this.senderCountry,
                    RECEIVER_CODE: this.receiverCode,
                    RECEIVER_CITY: this.receiverCity,
                    RECEIVER_PROVINCE: this.receiverProvince,
                    RECEIVER_COUNTRY: this.receiverCountry,
                    DETAILS: JSON.stringify({
                        weight: this.weight,
                        length: this.length,
                        width: this.width,
                        height: this.height,
                        volume: this.volume
                    }),
                    PRICE: this.price,
                    COURIER: this.courier,
                    ID_USER: this.userID
                });
                if(response) {
                    console.log(response.data);
                    this.shipment = {
                        ORDER_NUMBER : response.data.ORDER_NUMBER,
                        SENDER_CODE: response.data.SENDER_CODE,
                        RECEIVER_CODE: response.data.RECEIVER_CODE,
                        weight: this.weight,
                        length: this.length,
                        width: this.width,
                        height: this.height,
                        volume: this.volume, 
                        PRICE: response.data.PRICE,
                        COURIER: response.data.COURIER,
                        ID_USER: response.data.ID_USER
                    };
                    if(this.userID) {
                        // // const payload = { ID_USER: userID };
                        // // const headers = { 'Authorization': `Bearer ${localStorage.getItem('token')}` };
                        // // const userShipments = await axios.post("/shipments-by-user", payload, { headers });
                        // // console.log(userShipments);
                        // this.$router.push({name: 'myShipments', params: { userID: userID }});
                        console.log(response.data.ORDER_NUMBER);
                        console.log(response.data.SENDER_CODE);
                        console.log(response.data.RECEIVER_CODE);
                        console.log(this.weight);
                        console.log(this.length);
                        console.log(this.width);
                        console.log(this.height);
                        console.log(this.volume);
                        console.log(response.data.PRICE);
                        console.log(response.data.COURIER);
                        console.log(response.data.ID_USER);
                        console.log("push to checkout 1");
                        this.$router.push({ name: 'Checkout', params: { 
                                orderNumberPrice : response.data.ORDER_NUMBER+'_'+response.data.PRICE.toString().replace(".", "")+'_'+this.userID
                            }
                        });
                        
                    }
                    else {
                        console.log(response.data.ORDER_NUMBER);
                        console.log(response.data.SENDER_CODE);
                        console.log(response.data.RECEIVER_CODE);
                        console.log(this.weight);
                        console.log(this.length);
                        console.log(this.width);
                        console.log(this.height);
                        console.log(this.volume);
                        console.log(response.data.PRICE);
                        console.log(response.data.COURIER);
                        console.log(response.data.ID_USER);
                        console.log("push to checkout 2");
                        this.$router.push({ name: 'Checkout', params: { 
                                orderNumberPrice: response.data.ORDER_NUMBER+'_'+response.data.PRICE.toString().replace(".", "")+'_0'
                            }
                        });
                    }
                }
                else 
                    console.error("An error occurred before the insert shipment");
                
            }
            catch(error) {
                console.error("An error occurred during the insert shipment: ", error);
            }
        }
    }
}
</script>