<script>
import axios from '@/axios';
import { useRoute } from "vue-router";
const route = useRoute();

export default {
    mounted() {
        this.getShipment();
    },
    data() {
        return {
            senderCode: "",
            senderCity: "",
            sendererProvince: "",
            senderCountry: "",
            receiverCode: "",
            receiverCity: "",
            receiverProvince: "",
            receiverCountry: "",
            contact_details: {},
            price: null,
            courier: "",
            createdAt: ""
        }
    },
    methods: {
        async getShipment() {
            const user = await axios.get("https://arsil-backend-anzxy.sevalla.app/api/user", {
                headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`
                }
            });
            const payload = { orderNumber: this.$route.params.orderNumber, userID: user.data.id };
            const headers = { 'Authorization': `Bearer ${localStorage.getItem('token')}` };
            const response = await axios.post("/get-shipment-user", payload, { headers });
            console.log(response.data);
            this.price = response.data[0].PRICE;
            this.courier = response.data[0].COURIER;
            this.createdAt = response.data[0].created_at;
            this.senderCode = response.data[0].SENDER_CODE;
            this.receiverCode = response.data[0].RECEIVER_CODE;
            this.senderCity = response.data[0].SENDER_CITY;
            this.senderProvince = response.data[0].SENDER_PROVINCE;
            this.senderCountry = response.data[0].SENDER_COUNTRY;
            this.receiverCity = response.data[0].RECEIVER_CITY;
            this.receiverProvince = response.data[0].RECEIVER_PROVINCE;
            this.receiverCountry = response.data[0].RECEIVER_COUNTRY;
            this.contact_details = JSON.parse(response.data[0].CONTACT_DETAILS);
        }
    }
}
</script>