    <div class="container" v-if="this.$route.params.session_id.length < 5">
        <h1>Payed Unsuccessfully!</h1>
        <div class="row">
            <div class="col-md-4">
                <div class="form-container">
                    <h2>An error has occured during the payment process! Please try later!</h2>
                </div>
            </div>
        </div>
