<script>
import axios from '@/axios';
import { useRoute } from "vue-router";
const route = useRoute();

export default {
    mounted() {
        if(this.$route.params.session_id) 
            this.checkPayment();
    },
    data() {
        return {
            sessionId: this.$route.params.session_id,
            payedOrderNumber: this.$route.params.orderNumber,
            userId: this.$route.params.userId,
            timerCount: 8
        }
    },
    watch: {
        timerCount: {
            handler(value) {
                if (value > 0) {
                    setTimeout(() => {
                        this.timerCount--;
                    }, 1000);
                }
            },
            immediate: true // This ensures the watcher is triggered upon creation
        }
    },
    methods: {
        async checkPayment() {
            const payedSessionId = this.sessionId;
            if(payedSessionId.length > 5) {
                console.log('payedSessionId', payedSessionId);
                const response = await axios.post("/shipment-payed", {
                    orderNumber: this.payedOrderNumber,
                    payedSessionId: payedSessionId
                });
                console.log(response.data);
            }
            if(eval(this.$route.params.userId) > 0) {
                // // const payload = { ID_USER: userID };
                // // const headers = { 'Authorization': `Bearer ${localStorage.getItem('token')}` };
                // // const userShipments = await axios.post("/shipments-by-user", payload, { headers });
                // // console.log(userShipments);
                setTimeout(() => {
                    this.$router.push({name: 'myShipments', params: { userID: this.$route.params.userId }});
                }, 8000);
                console.log("this.route.params.orderNumber", this.$route.params.orderNumber);
                console.log("this.route.params.session_id", this.$route.params.session_id);
                console.log("this.route.params.userId", this.$route.params.userId);
            }
        },
    }
}
</script>