<template>
    
</template>

<script>
import axios from '@/axios';
export default {
    mounted() {
        this.logoutUser();
    },
    data() {
        return {
            user: {}
        }
    },
    methods: {
        async logoutUser() {
            console.error("START logout method function Logout.vue Componenet");
            try {
                const user = await axios.get("https://arsil-backend-anzxy.sevalla.app/api/user", {
                    headers: {
                        'Authorization': `Bearer ${localStorage.getItem('token')}`
                    }
                });
                const payload = { user: user };
                const headers = { 'Authorization': `Bearer ${localStorage.getItem('token')}` };
                const response = await axios.post("/logout", payload, { headers });
                console.log(response);
                // if(response.data) 
                //     this.$store.commit('LOGOUT');
            }
            catch(error) {
                console.error("An error occurred during the logout: ", error.response.data);
            }
        }
    }
};
</script>